.stars {

  &__star {
    background-image:url(../images/star.svg);
    width: 16px;
    height: 16px;
    background-position: 50%;
    background-repeat: no-repeat;
    margin-left: 4px;

    &:first-child {
      margin-left: 0;
    }
 }

  &--1 :nth-child(-n +1),
  &--2 :nth-child(-n +2),
  &--3 :nth-child(-n +3),
  &--4 :nth-child(-n +4),
  &--5 :nth-child(-n +5) {
    background-image:url(../images/star-active.svg);
  }
}
